<template>
    <section class="device-info">
        <el-switch
            class="switch"
            :width="100"
            @change="refreshSwitch"
            v-model="isRefresh"
            on-text="刷新已开启"
            off-text="刷新已关闭"
            on-color="#13ce66"
            off-color="#ff4949">
        </el-switch>
        <el-tabs v-model="activeName" class="tabs">
            <el-tab-pane label="现场图" name="first" class="first-pane">
                <!-- gate-nav -->
                <div class="gate" >
                    <img src="../../assets/imgs/gate-nav-b.jpg" alt="">
                    <ul class="gate-nav" >
                        <li v-for="(row, index) in lists" @click="handleClick(index)" :class="{
                        'red': row.bg_b == '故障' || row.road_block == '故障' ,
                        'gray': row.type === null || !row.valid,
                        'orange': row.ir_a1 == '故障' || row.ir_a2 == '故障'||row.ir_a3 == '故障' || row.ir_a4 == '故障'|| row.ir_b1 == '故障' || row.ir_b2 == '故障'|| row.ipc == '故障'
                    }" >
                            <div class="number" >{{row.chnl_no}}</div>
                        </li>
                    </ul>
                </div>

                <div class="status-img" >
                    <p>{{gateRow}}</p>
                    <div class="tooltip">
                        <img src="../../assets/imgs/gate-status.png" alt="">
                        <span class="bg_a" v-if="rowInfos.bg_b == '故障' " :class="{blink: rowInfos.bg_b =='故障'}"></span>
                        <span class="sl_a1" v-if="rowInfos.sl_a1 == '有车' ">
                             <img src="../../assets/imgs/gate.png" alt="">
                        </span>
                        <span class="sl_a2" v-if="rowInfos.sl_a2 == '有车' "></span>
                        <span class="ir_a1 ir_a" v-if="rowInfos.ir_a1 == '无车' " :class="{blink: rowInfos.ir_a1 =='故障'}"></span>
                        <span class="ir_a2 ir_a" v-if="rowInfos.ir_a2 == '无车' " :class="{blink: rowInfos.ir_a2 =='故障'}"></span>
                        <span class="ir_a3 ir_a" v-if="rowInfos.ir_a3 == '无车' " :class="{blink: rowInfos.ir_a3 =='故障'}"></span>
                        <span class="ir_a4 ir_a" v-if="rowInfos.ir_a4 == '无车' " :class="{blink: rowInfos.ir_a4 =='故障'}"></span>
                        <span class="ir_b1 ir_a" v-if="rowInfos.ir_b1 == '无箱' " :class="{blink: rowInfos.ir_b1 =='故障'}"></span>
                        <span class="ir_b2 ir_a" v-if="rowInfos.ir_b2 == '无箱' " :class="{blink: rowInfos.ir_b2 =='故障'}"></span>
                        <span class="alarm-red" v-if="rowInfos.alarm_lamp == '亮' "></span>
                        <span class="alarm-green" v-if="rowInfos.alarm_lamp == '灭' "></span>
                        <span class="road_block" v-if="rowInfos.road_block == '路障关' || rowInfos.road_block == '故障'"
                              :class="{blink: rowInfos.road_block =='故障'}">
                </span>
                    </div>
                </div>
                <el-card class="card">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="通道号">
                            <span>{{ rowInfos.chnl_no }}</span>
                        </el-form-item>
                        <el-form-item label="道闸状态">
                            <span :class="rowInfos.bg_b == '故障'?'error':''">{{ rowInfos.bg_b }}</span>
                        </el-form-item>
                        <el-form-item label="创建时间">
                            <span>{{ rowInfos.rc_time }}</span>
                        </el-form-item>
                        <el-form-item label="工控机">
                            <span :class="rowInfos.ipc !== '正常'?'error':''">{{ rowInfos.ipc }}</span>
                        </el-form-item>
                        <el-form-item label="串口">
                            <span :class="rowInfos.com_hub !== '正常'?'error':''">{{ rowInfos.com_hub }}</span>
                        </el-form-item>
                        <el-form-item label="读卡器">
                            <span :class="rowInfos.ic_reader !== '正常'?'error':''">{{ rowInfos.ic_reader }}</span>
                        </el-form-item>
                        <el-form-item label="条码扫描器">
                            <span :class="rowInfos.tm_reader !== '正常'?'error':''">{{ rowInfos.tm_reader }}</span>
                        </el-form-item>
                        <el-form-item label="LED显示屏">
                            <span :class="rowInfos.plc !== '正常'?'error':''">{{ rowInfos.led_monitor }}</span>
                        </el-form-item>
                        <el-form-item label="报警灯状态">
                            <span :class="rowInfos.alarm_lamp == '亮'?'error':''">{{ rowInfos.alarm_lamp }}</span>
                        </el-form-item>
                        <el-form-item label="地磅">
                            <span :class="rowInfos.loadometer !== '正常'?'error':''">{{ rowInfos.loadometer }}</span>
                        </el-form-item>
                        <el-form-item label="路障机">
                            <span :class="rowInfos.road_block == '故障'?'error':''">{{ rowInfos.road_block }}</span>
                        </el-form-item>
                        <el-form-item label="出口道闸地感">
                            <span :class="rowInfos.sl_a1 == '有车'?'error':''">{{ rowInfos.sl_a1 }}</span>
                        </el-form-item>
                        <el-form-item label="出口路障地感">
                            <span :class="rowInfos.sl_a2 == '有车'?'error':''">{{ rowInfos.sl_a2 }}</span>
                        </el-form-item>
                        <el-form-item label="入口道闸地感">
                            <span>{{ rowInfos.sl_b1 }}</span>
                        </el-form-item>
                        <el-form-item label="防砸红外1">
                            <span :class="rowInfos.ir_a1 == '有车'?'error':''">{{ rowInfos.ir_a1 }}</span>
                        </el-form-item>
                        <el-form-item label="防砸红外2">
                            <span :class="rowInfos.ir_a2 == '有车'?'error':''">{{ rowInfos.ir_a2 }}</span>
                        </el-form-item>
                        <el-form-item label="防砸红外3">
                            <span :class="rowInfos.ir_a3 == '有车'?'error':''">{{ rowInfos.ir_a3 }}</span>
                        </el-form-item>
                        <el-form-item label="防砸红外4">
                            <span :class="rowInfos.ir_a4 == '有车'?'error':''">{{ rowInfos.ir_a4 }}</span>
                        </el-form-item>
                        <el-form-item label="箱型检测红外1">
                            <span :class="rowInfos.ir_b1 == '有车'?'error':''">{{ rowInfos.ir_b1 }}</span>
                        </el-form-item>
                        <el-form-item label="箱型检测红外2">
                            <span :class="rowInfos.ir_b2 == '有车'?'error':''">{{ rowInfos.ir_b2 }}</span>
                        </el-form-item>
                    </el-form>
                </el-card>
            </el-tab-pane>
            <el-tab-pane label="表格图" name="second">
                <el-table :data="lists"
                          v-loading="listLoading"
                          border
                          class="list"
                          :row-style="tableRowStyle"
                >
                    <el-table-column type="index" width="60" fixed="left"></el-table-column>
                    <el-table-column prop="bg_b" label="道闸状态" width="100">
                        <template scope="scope">
                            <span :class="scope.row.bg_b == '故障'?'error':''">{{ scope.row.bg_b }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="chnl_no" label="通道号" width="80"></el-table-column>
                    <el-table-column prop="rc_time" label="创建时间" width="180"></el-table-column>
                    <el-table-column prop="ipc" label="工控机" width="100">
                        <template scope="scope">
                            <span :class="scope.row.ipc !== '正常'?'error':''">{{ scope.row.ipc }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="com_hub" label="串口" width="100">
                        <template scope="scope">
                            <span :class="scope.row.com_hub !== '正常'?'error':''">{{ scope.row.com_hub }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ic_reader" label="读卡器" width="100">
                        <template scope="scope">
                            <span :class="scope.row.ic_reader !== '正常'?'error':''">{{ scope.row.ic_reader }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="tm_reader" label="条码扫描器" width="115">
                        <template scope="scope">
                            <span :class="scope.row.tm_reader !== '正常'?'error':''">{{ scope.row.tm_reader }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="led_monitor" label="LED显示屏" width="120"></el-table-column>
                    <el-table-column prop="plc" label="PLC" width="115">
                        <template scope="scope">
                            <span :class="scope.row.plc !== '正常'?'error':''">{{ scope.row.plc }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="alarm_lamp" label="报警灯状态" width="115">
                        <template scope="scope">
                            <span :class="scope.row.alarm_lamp == '亮'?'error':''">{{ scope.row.alarm_lamp }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="loadometer" label="地磅" width="115">
                        <template scope="scope">
                            <span :class="scope.row.loadometer !== '正常'?'error':''">{{ scope.row.loadometer }}</span>
                        </template>
                    </el-table-column>
                    <!--出口处道闸-->
                    <el-table-column prop="road_block" label="路障机" width="115">
                        <template scope="scope">
                            <span :class="scope.row.road_block == '故障'?'error':''">{{ scope.row.road_block }}</span>
                        </template>
                    </el-table-column>
                    <!--入口处道闸-->
                    <el-table-column prop="sl_a1" label="出口道闸地感" width="122">
                        <template scope="scope">
                            <span :class="scope.row.sl_a1 == '有车'?'error':''">{{ scope.row.sl_a1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="sl_a2" label="出口路障地感" width="122">
                        <template scope="scope">
                            <span :class="scope.row.sl_a2 == '有车'?'error':''">{{ scope.row.sl_a2 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="sl_b1" label="入口道闸地感" width="122">
                        <template scope="scope">
                            <span :class="scope.row.sl_b1 == '有车'?'error':''">{{ scope.row.sl_b1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ir_a1" label="防砸红外1" width="115">
                        <template scope="scope">
                            <span :class="scope.row.ir_a1 == '有车'?'error':''">{{ scope.row.ir_a1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ir_a2" label="防砸红外2" width="115">
                        <template scope="scope">
                            <span :class="scope.row.ir_a2 == '有车'?'error':''">{{ scope.row.ir_a2 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ir_a3" label="防砸红外3" width="115">
                        <template scope="scope">
                            <span :class="scope.row.ir_a3 == '有车'?'error':''">{{ scope.row.ir_a3 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ir_a4" label="防砸红外4" width="115">
                        <template scope="scope">
                            <span :class="scope.row.ir_a4 == '有车'?'error':''">{{ scope.row.ir_a4 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ir_b1" label="箱型检测红外1" width="130">
                        <template scope="scope">
                            <span :class="scope.row.ir_b1 == '有箱'?'error':''">{{ scope.row.ir_b1 }}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="ir_b2" label="箱型检测红外2" width="130">
                        <template scope="scope">
                            <span :class="scope.row.ir_b2 == '有箱'?'error':''">{{ scope.row.ir_b2 }}</span>
                        </template>
                    </el-table-column>

                    <!--<el-table-column prop="seq_no" label="序列号" min-width="200" ></el-table-column>
                    <el-table-column prop="i_e_type" label="进出标志" width="100"></el-table-column>-->
                </el-table>
            </el-tab-pane>
        </el-tabs>

    </section>

</template>

<script>
    import { getDeviceInfo } from '@/api/api'

    export default {
        data () {
            return {
                value1: true,
                value2: false,
                token: localStorage.getItem('token'),
                lists: [],
                listLoading: false,
                timer: null,
                isRefresh: true,
                gateRow: '通道1状态图信息',
                select: 0,
                activeName: 'first'
            }
        },
        computed: {
            rowInfos () {
                return this.lists[this.select]
            },
        },
        methods: {
            prevent (ev) {
                ev.preventDefault()
                return false
            },
            getLists () {
                this.listLoading = true
                getDeviceInfo({
                    token: this.token,
                    gate_id: 'B'
                }).then((res) => {
                    this.lists = res.data
                    this.listLoading = false
                })
            },
            refreshSwitch () {
                if (this.isRefresh) {
                    this.timer = setInterval(() => {
                        this.getLists()
                    }, 5000)
                }
                else {
                    clearInterval(this.timer)
                }

            },
            tableRowStyle (row, index) {
//
                if (!row.valid) {
                    return {
                        background: '#e4e8f1',
                        cursor: 'not-allowed',
                        color:'rgba(0,0,0,0.8)'
                    }
                }

            },
            handleClick (index) {
                console.log(index)
                this.gateRow = `通道${index +1}状态图信息`
                this.select = index
            },
        },
        created () {
            this.getLists()
            this.refreshSwitch()
        },
        beforeDestroy () {
            clearInterval(this.timer)
        }
    }

</script>
<style scoped lang="scss">
    .device-info {
        margin-top: 10px;
        .switch {
            margin: 10px 0;
        }
        .tabs{
            width: 100%;
            .first-pane{
                width: 100%;
                overflow-x: hidden;
            }
        }
        .card{
            width: 99%;
            // 源码使用学习之处
            .demo-table-expand {
                font-size: 0;
            }
            .demo-table-expand label {
                width: 100px;
                color: #99a9bf;
            }
            .demo-table-expand .el-form-item {
                margin-right: 0;
                margin-bottom: 0;
                width: 25%;
            }
            .error {
                color: #fff;
                background-color: rgb(255, 73, 73);
                display: inline-block;
                width: 100%;
                line-height: 25px;
                height: 25px;
                padding: 0 3px;
                text-align: center;
            }
        }
        .gate {
            width: 1525px;
            margin: 10px auto;
            position: relative;
            zoom: 0.6;
            img {
                width: 100%;
                height: 100%;
            }
            .gate-nav {
                position: absolute;
                width: 1525px;
                top: 1px;
                left: 0;
                display: flex;
                flex-direction: row-reverse;
                li {
                    width: 45px;
                    margin-right: 23px;
                    height: 232px;
                    border: 4px solid #fff;
                    background-color: rgba(0, 255, 0, 0.7);
                    text-align: center;
                    font-size: 25px;
                    cursor: pointer;
                    &.orange{
                        background-color: rgba(245,185,76,0.9);
                    }
                    &.red{
                        background-color: rgba(255,0,0,0.9);
                        animation: blink 800ms infinite;
                    }
                    &.gray{
                        background-color: rgba(214,214,214,0.9);
                        animation: none 0;
                    }
                    .number {
                       margin-top: 3px;

                    }
                    &:first-child{
                        margin-right: 0;
                    }
                    &:nth-child(3) {
                        width: 50px;
                    }
                    &:nth-child(4) {
                        width: 50px;
                        margin-left: 10px;
                    }
                    &:nth-child(5) {
                        width: 50px;
                    }
                    &:nth-child(6) {
                        width: 50px;
                    }
                    &:nth-child(7) {
                        width: 50px;
                    }
                    &:nth-child(8) {
                        width: 50px;
                        margin-left: 120px;
                    }
                    &:nth-child(9) {
                        width: 55px;
                    }
                    &:nth-child(10) {
                        width: 50px;
                    }
                    &:nth-child(11) {
                        width: 50px;
                    }
                    &:nth-child(12) {
                        width: 50px;
                        margin-left: 16px;
                    }
                    &:nth-child(14), &:nth-child(15), &:nth-child(16), &:nth-child(17), &:nth-child(18), &:nth-child(19), &:nth-child(20){
                        width: 38px;
                    }
                    &:nth-child(14){
                        margin-left: 15px;
                    }
                }
            }
        }
        .status-img {
            display: flex;
            width:1525px ;
            flex-direction: column;
            align-items: center;
            margin: 5px auto;
            margin-bottom: 15px;
            zoom: 0.6;
            p {
                font-size: 20px;
                font-weight: bold;
                margin-top: 10px;
            }
            .tooltip {
                width: 1523px;
                height: 509px;
                position: relative;
                span {
                    display: inline-block;
                    background-color: rgba(255, 0, 0, 0.7);
                    position: absolute;
                    &.sl_a1 {
                        width: 25px;
                        height: 256px;
                        left: 435px;
                        top: 175px;
                        border-radius: 15px;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                    }

                    &.bg_a {
                        width: 60px;
                        height: 44px;
                        left: 420px;
                        top: 120px;
                        background-color: rgba(255, 0, 0, 0.9);
                    }
                    &.sl_a2 {
                        width: 87px;
                        height: 208px;
                        left: 71px;
                        top: 204px;
                        border-radius: 25px;
                    }
                    &.road_block {
                        width: 56px;
                        height: 227px;
                        left: 273px;
                        top: 190px;
                    }
                    &.alarm-red {
                        width: 35px;
                        height: 35px;
                        border-radius: 50%;
                        left: 303px;
                        top: 76px;
                        background-color: rgba(255, 0, 0, 0.9);
                    }
                    &.alarm-green {
                        width: 35px;
                        height: 35px;
                        border-radius: 50%;
                        left: 303px;
                        top: 116px;
                        background-color: rgba(21, 245, 44, 1);
                    }
                    &.ir_a{
                        width: 18px;
                        height: 42px;
                        &:before {
                            content: '';
                            display: inline-block;
                            background-color: rgba(255, 0, 0, 0.7);
                            position: absolute;
                            width: 18px;
                            height: 42px;
                        }
                    }
                    &.ir_a1 {
                        left: 199px;
                        top: 105px;
                        &:before {
                            left: 0px;
                            top: 352px;
                        }
                    }
                    &.ir_a2 {
                        left: 239px;
                        top: 128px;
                        &:before {
                            left: 0px;
                            top: 352px - 42px;
                        }
                    }
                    &.ir_a3 {
                        left: 199px + 288px +18px;
                        top: 102px;
                        &:before {
                            left: 0px;
                            top: 353px;
                        }
                    }
                    &.ir_a4 {
                        left: 239px + 288px + 18px;
                        top: 128px - 2px;
                        &:before {
                            left: 0px;
                            top: 352px - 42px;
                        }
                    }
                    &.ir_b1 {
                        left: 239px + 288px + 18px + 371px + 18px;
                        top: 128px - 7px;
                        &:before {
                            left: 0px;
                            top: 352px - 22px;
                        }
                    }
                    &.ir_b2 {
                        width: 19px;
                        left: 239px + 288px + 18px + 371px + 18px + 143px;
                        top: 128px - 7px;
                        &:before {
                            left: 0px;
                            top: 352px - 22px;
                        }
                    }
                }
            }
        }
        .list {
            width: 100%;
            .error {
                color: #fff;
                background-color: rgb(255, 73, 73);
                display: inline-block;
                width: 100%;
                height: 100%;
                text-align: center;
            }
        }
    }

    .blink {
        animation: blink 800ms infinite;
    }

    @keyframes blink {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
</style>


